@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
*{
    margin:0;
    padding:0;
    font-family: 'Jost', sans-serif;
}


body{
    background:#f3f2ef;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family: 'Jost', sans-serif;
}

html{
    font-size: 100%;
    height: 100%;
}

html{
    background: #eee;

}
.box{
    width:25vw;
    height:60vh;
    border-radius: 0.5rem;
    box-shadow:0 0.2rem 3rem rgba(0,0,0, 0.2);
    background: #a5bbdd;
    position:relative;
    overflow: hidden;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    min-width: 20rem;
    min-height: 35rem;
}
.wave{
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left:50%;
    background: white;
    width:50rem;
    height:50rem;
    margin-left:-25rem ;
    margin-top: -20rem;
    -webkit-transform-origin: 50% 48%;
            transform-origin: 50% 48%;
    border-radius: 43%;
    -webkit-animation: drift 3000ms infinite linear ;
            animation: drift 3000ms infinite linear ;
    z-index: 1;
    
}

.wave.-there{ 
    -webkit-animation: drift 5000ms infinite linear ; 
            animation: drift 5000ms infinite linear ;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave.-two{ 
    -webkit-animation: drift 7000ms infinite linear ; 
            animation: drift 7000ms infinite linear ;
    z-index: 3 !important;
    opacity: 0.1;
}
.box:after{
    content:"";
    display: block;
    left:0;
    top:0;
    width:100%;
    height:100%;
    z-index: 11;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);

}
@-webkit-keyframes drift{
    from{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    from{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes drift{
    from{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    from{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.info{
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    width:100%;
    height:100%;
    z-index:4;
}
.location{
    margin-top: -5.5rem;
    text-align: center;
    font-weight: 800;
    font-size: 5rem;
    text-transform: capitalize;
}
.fa-street-view{
    -webkit-animation:rotates 3s linear infinite alternate;
            animation:rotates 3s linear infinite alternate;
}
@-webkit-keyframes rotates{
    from{
        -webkit-transform: translateX(-0.5rem);
                transform: translateX(-0.5rem);
    }
    from{
        -webkit-transform: translateX(0.5rem);
                transform: translateX(0.5rem);
    }
}
@keyframes rotates{
    from{
        -webkit-transform: translateX(-0.5rem);
                transform: translateX(-0.5rem);
    }
    from{
        -webkit-transform: translateX(0.5rem);
                transform: translateX(0.5rem);
    }
}
#date{
    text-align:center;
    margin-top: 0.5rem;
    color:#57686f;
    font-size: 1.2rem;
    font-weight: 300;
    text-transform: uppercase;
}
.temp{
    margin-top: 2.5rem;
    text-align: center;
    font-size: 3rem;
}
.tempmin_max{
    text-align: center;
    margin-top: 0.3rem;
    font-weight: 300;
    font-size:1.2rem;
    color:#57606f;
    
}
#weathercon{
    height:55%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 3rem;
}
#weathercon .fas{
    font-size: 6rem;
    -webkit-animation: fas-anime 3s linear infinite alternate;
            animation: fas-anime 3s linear infinite alternate;

}
@-webkit-keyframes fas-anime{
    from{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    from{
        -webkit-transform: scale(.5);
                transform: scale(.5);
    }
}
@keyframes fas-anime{
    from{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    from{
        -webkit-transform: scale(.5);
                transform: scale(.5);
    }
}

@media (max-width:600px){
    .box{
        width: 90vh;
        height:80vh;
    }
    .wave{
        top:85%; 
    }
    #weathercon{
        font-size:5rem;
    }
    .info{
        font-size:1.5rem;
    }
}
@media (max-width:500px){
    .box{
        height:80vh;
    }
    .wave{
        top:115%;
    }
}
body > span{
    width:100vw;
    text-align: center;
    color:gray;
}

.inputData{
    width:100%;
    height:10vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.inputField{
    width:50%;
    margin:auto;
    border-radius: 5rem;
    padding:1rem 1rem;
    outline:none;
    font-size: 1.5rem;
}
.errorMsg{
    text-align: center;
    font-size: 2rem;
    margin:2rem auto;
}
